class ApiCaller {

    static call(url, data, callback, errorCallback, onResp) {
        console.log(url);
        fetch(url, {
            method: 'POST',
            headers: {
                'Origin': 'http://localhost:8080',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept, Set-cookies, Authorization'
            },
            body: JSON.stringify(data),
            mode: "cors",
        })
            .then(res => {
                if (onResp !== undefined) {
                    return onResp(res);
                } else {
                    return res.json();
                }
            })
            .then((data) => {
                    callback(data);
            })
            .catch((data) => {
                if (errorCallback !== undefined) {
                    errorCallback(data);
                } else {
                    console.log(data);
                }
            })
    }
}

export default ApiCaller;