import Paper from "@material-ui/core/Paper/Paper";
import Table from "@material-ui/core/Table/Table";
import TableHead from "@material-ui/core/TableHead/TableHead";
import TableRow from "@material-ui/core/TableRow/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import DeleteOutlinedIcon from '@material-ui/icons/DeleteOutlined';
import TableBody from "@material-ui/core/TableBody/TableBody";
import TableContainer from "@material-ui/core/TableContainer/TableContainer";
import React from "react";
import ApiCaller from "../../api/ApiCaller";
import UrlConst from "../../const/UrlConst";

class MachineTable extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: []
        };
    }

    componentDidMount() {
        ApiCaller.call(UrlConst.GET_ALL_MACHINES, null, (data) => {
            this.setState({data: data.machineDTOList});
        });
    }

    refresh() {
        ApiCaller.call(UrlConst.GET_ALL_MACHINES, null, (data) => {
            this.setState({data: data.machineDTOList});
        });
    }

    render() {
        const rows = this.getRows();

        return (
            <TableContainer component={Paper}>
                <Table aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>ID</TableCell>
                            <TableCell align="right">VIN</TableCell>
                            <TableCell align="right">Type</TableCell>
                            <TableCell align="right">State</TableCell>
                            <TableCell align="right">Výrobce</TableCell>
                            <TableCell align="right">Online</TableCell>
                            <TableCell align="right">Akce</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows}
                    </TableBody>
                </Table>
            </TableContainer>);
    }

    getRows() {
        let data = this.state.data;
        return data.map((value, key) => {

            return (
                <TableRow key={value.id}>
                    <TableCell component="th" scope="row">
                        {value.id}
                    </TableCell>
                    <TableCell align="right">{value.vin}</TableCell>
                    <TableCell align="right">{value.description}</TableCell>
                    <TableCell align="right">{value.machineTypeEntity}</TableCell>
                    <TableCell align="right">{value.machineEntityState}</TableCell>
                    <TableCell align="right">{value.manufacturer}</TableCell>
                    <TableCell align="right">{(value.online)? "Ano":"Ne"}</TableCell>
                    <TableCell align={"right"}><DeleteOutlinedIcon onClick={
                        (e)=> {
                            this.deleteMachine(value.vin);
                        }
                    }/></TableCell>
                </TableRow>);
        });
    }

    deleteMachine(vin) {
        ApiCaller.call(UrlConst.REMOVE_MACHINE ,{vin:vin}, (data) => {
            this.refresh()
        });
    }
}

export default MachineTable;