import InputLabel from "@material-ui/core/InputLabel/InputLabel";
import React from "react";
import GridItem from "../Grid/GridItem";
import Card from "../Card/Card";
import CardBody from "../Card/CardBody";
import GridContainer from "../Grid/GridContainer";
import CardFooter from "../Card/CardFooter";
import Button from "../CustomButtons/Button";
import Select from "@material-ui/core/Select/Select";
import MenuItem from "@material-ui/core/MenuItem/MenuItem";
import TextField from "@material-ui/core/TextField/TextField";
import {AddAlert} from "@material-ui/icons";
import Snackbar from "@material-ui/core/Snackbar/Snackbar";
import ApiCaller from "../../api/ApiCaller";
import UrlConst from "../../const/UrlConst";

class CreateDeviceForm extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            vin: "",
            name: "",
            manufactor: "",
            type: "TRACTOR",
            description: "",
            message: null
        }
    }

    getMessage() {
        if (this.state.message !== null)
            return (<Snackbar
                place="bc"
                color="info"
                icon={AddAlert}
                message={this.state.message}
                open={true}
                onClick={() => {
                    this.setState({message: null})
                }}
            />);
        else {
            return (<div></div>);
        }
    }

    render() {
        const message = this.getMessage();
        return (
            <div>
                {message}
                <Card>
                    <CardBody>
                        <GridContainer>
                            <GridItem xs={12} sm={12} md={3}>
                                <TextField id="standard-basic" label="Vin Kód"
                                           value={this.state.vin}
                                           onChange={this.onBlureVin.bind(this)}
                                />
                            </GridItem>
                            <GridItem xs={12} sm={12} md={4}>
                                <TextField id="standard-basic" label="Název"
                                           value={this.state.name}
                                           onChange={this.onBlureName.bind(this)}
                                />
                            </GridItem>

                            <GridItem xs={12} sm={12} md={4}>
                                <TextField id="standard-basic" label="Výrobce"
                                           value={this.state.manufactor}
                                           onChange={this.onBlureManufacture.bind(this)}
                                />
                            </GridItem>
                            <GridItem xs={12} sm={12} md={1}>
                                <InputLabel htmlFor="type">Typ</InputLabel>
                                <Select
                                    id="demo-controlled-open-select"
                                    labelId="type"
                                    value={this.state.type}
                                    onChange={this.handleSelectChange.bind(this)}
                                    style={{"width": "auto"}}
                                >
                                    <MenuItem value={"TRACTOR"}>Traktor</MenuItem>
                                    <MenuItem value={"COMBINE"}>Kombajn</MenuItem>
                                    <MenuItem value={"CAR"}>Auto</MenuItem>
                                    <MenuItem value={"TRACK"}>Nákladák</MenuItem>
                                    <MenuItem value={"GRADER"}>Srovnávač</MenuItem>
                                </Select>
                            </GridItem>
                        </GridContainer>
                        <GridContainer>
                            <GridItem style={{"marginTop": "10px", "marginBottom": "10px"}}/>
                        </GridContainer>
                        <GridContainer style={{"marginTop": "10px"}}>
                            <GridItem xs={12} sm={12} md={12}>
                                <TextField
                                    id="standard-multiline-flexible"
                                    label="Popis zarizeni"
                                    multiline
                                    onChange={this.onBlureDescription.bind(this)}
                                    value={this.state.description}
                                    rowsMax={4}
                                    fullWidth
                                />
                            </GridItem>
                        </GridContainer>
                    </CardBody>
                    <CardFooter>
                        <Button color="rose" onClick={this.sendData.bind(this)}>Vytvořit stroj</Button>
                    </CardFooter>
                </Card>
            </div>
        );
    }

    sendData() {

        this.clearAll();
        ApiCaller.call(UrlConst.CREATE_MACHINE,
            {
                vin: this.state.vin,
                name: this.state.name,
                description: this.state.description,
                manufacturer: this.state.manufactor,
                machineTypeEntity: this.state.type
            }, () => {
                this.setState({message: "Stroj byl zalozen."})
                this.props.table.current.refresh();
            });

    }

    onBlureVin(event) {
        let value = event.target.value;
        this.setState({vin: value});
    }

    onBlureName(event) {
        let value = event.target.value;
        this.setState({name: value});
    }

    onBlureManufacture(event) {
        let value = event.target.value;
        this.setState({manufactor: value});
    }

    handleSelectChange(event) {
        let value = event.target.value;
        this.setState({type: value});
    }

    onBlureDescription(event) {
        let value = event.target.value;
        this.setState({description: value});
    }

    clearAll() {
        this.setState({
            vin: "",
            name: "",
            manufactor: "",
            type: "TRACTOR",
            description: ""
        });
    }

}

export default CreateDeviceForm;